.nav-wrapper{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  z-index: 999;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}
.nav-item {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: center;
      justify-content: center;
  text-decoration: none;
  font-size: var(--nav-item-fontSize);
  -webkit-transition: font-weight 0.2s, -webkit-transform 0.25s ease;
  transition: font-weight 0.2s, -webkit-transform 0.25s ease;
  -o-transition: transform 0.25s ease, font-weight 0.2s;
  transition: transform 0.25s ease, font-weight 0.2s;
  transition: transform 0.25s ease, font-weight 0.2s, -webkit-transform 0.25s ease;
  color: #7c95a0;
}
.nav-item:hover {
  font-weight: 700;
  -webkit-transform: scale(1.08);
      -ms-transform: scale(1.08);
          transform: scale(1.08);
  cursor: pointer;
}
.nav-active {
  color: #4b5b66;
}


