.logout-wrapper {
  display: block;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: auto;
  height: auto;
}

.logout-label {
  font-size: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 9px;
}

.buttons {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin-top: 30px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.logout-button {
  padding: 10px;
  width: 130px;
  font-size: 25px;
  margin: 0 10px;
  border: 5px solid #e8f4fc;
  border-radius: 30px;
  -webkit-transition: font-weight 0.5s ease, color 0.3s ease, -webkit-transform 0.3s ease;
  transition: font-weight 0.5s ease, color 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: font-weight 0.5s ease, transform 0.3s ease, color 0.3s ease;
  transition: font-weight 0.5s ease, transform 0.3s ease, color 0.3s ease;
  transition: font-weight 0.5s ease, transform 0.3s ease, color 0.3s ease, -webkit-transform 0.3s ease;
}

.logout-button:hover {
  font-weight: 900;
  cursor: pointer;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.logout-confirm {
  color: #427e42;
}

.logout-cancel {
  color: #925858;
}

.logout-confirm:hover {
  color: #009100;
}

.logout-cancel:hover {
  color: #a50000;
}


